import React from "react"
import Breadcrumbs from "../../../../components/@vuexy/breadCrumbs/BreadCrumb"
import Configment from "./configment"

class MarkPage extends React.Component {

  render() {
    return (
      <React.Fragment>
        <Breadcrumbs
          breadCrumbTitle="Mark"
          breadCrumbParent="Areas"
          breadCrumbActive="Mark"
        />
        <Configment pageType="mark" {...this.props} />
      </React.Fragment>
    )
  }
}

export default MarkPage