import React from 'react';
import { Card, CardBody, Row, Col,
  UncontrolledButtonDropdown,
  ButtonDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem
} from 'reactstrap';
import PropTypes from 'prop-types';
import Select from 'react-select';
import { connect } from 'react-redux';
import Checkbox from "../../components/@vuexy/checkbox/CheckboxesVuexy"
import { Check, ChevronDown } from "react-feather"
import Flatpickr from "react-flatpickr";
import { setValues } from "./../../redux/actions/selectTopic";
import "../../assets/scss/plugins/forms/react-select/_react-select.scss";
import "flatpickr/dist/themes/light.css";
import "../../assets/scss/plugins/forms/flatpickr/flatpickr.scss";

class SelectTopic extends React.Component {
  static getDerivedStateFromProps(props, state) {
    if(props.topicId != state.selectedTopicId) {
      console.log("---------------------------------");
      console.log("props.topicId", props.topicId);
      console.log("state.selectedTopicId", state.selectedTopicId);
      console.log("---------------------------------");
      return {
        selectedTopicId: props.topicId
      }
    }
    return null
  }
  constructor(props) {
    super(props);
    this.state = {
      selectedLevelId: "",
      selectedCategoryId: "",
      selectedTopicId: "",
      MCQ: "",
      rangePicker: ''
    }
  }

  componentDidUpdate(prevProps, prevState) {
    if(prevState.selectedTopicId !== this.state.selectedTopicId) {
      if(this.state.selectedTopicId) {
        this.setValuesByTopicId(this.state.selectedTopicId)
      }
    }
  }

  handleChange = (name, value) => {
    if(name === "selectedLevelId" || name === "selectedCategoryId") {
      this.setState({
        selectedTopicId: ''
      })
    }
    if(name === "selectedTopicId" || name === "selectedTopicId") {
      this.props.setValues({
        topicId: value
      });
      if(value) {
        this.setValuesByTopicId(value)
      }
    } else {
      this.props.setValues({
        topicId: ""
      });
    }
    this.setState({
      [name]: value
    }, () => {
      this.props.onChange && this.props.onChange({
        selectedLevelId: this.state.selectedLevelId,
        selectedCategoryId: this.state.selectedCategoryId,
        selectedTopicId: this.state.selectedTopicId,
        MCQ: this.state.MCQ,
        rangePicker: this.state.rangePicker
      })
    });
  }

  setValuesByTopicId = (value) => {
    let topic = this.props.topics.find((item) => item._id === value);

    let levelId = topic.levelId._id
    let categoryId = topic.categoryId._id

    return this.setState({
      selectedTopicId: value,
      selectedLevelId: levelId,
      selectedCategoryId: categoryId
    }, () => {
      this.props.onChange && this.props.onChange({
        selectedLevelId: levelId,
        selectedCategoryId: categoryId,
        selectedTopicId: value,
        MCQ: this.state.MCQ,
        rangePicker: this.state.rangePicker
      })
    })
  }

  renderCategories = (categories) => {
    let result = [];

    for (const category of categories) {
      result.push({
        value: category._id,
        label: category.name,
        isDisabled: category.isParent
      })
      if(category.children.length > 0) {
        for (const child of category.children) {
          result.push({
            value: child._id,
            label: <span className='pl-1'>{child.name}</span>,
          })
        }
      }
    }
    return result;
  }

  render() {
    const { levels, categories, topics } = this.props;
    const { selectedCategoryId, selectedLevelId, selectedTopicId, rangePicker, MCQ  } = this.state;

    let levelOptions = levels.map((level) => ({value: level._id, label: level.name}))
    let categoriesOptions = this.renderCategories(categories)

    let topicsOptions = topics.filter((topic) => {
        return (topic.levelId._id === selectedLevelId || selectedLevelId === "") && (topic.categoryId._id === selectedCategoryId || selectedCategoryId === "")
      }).map((elt) => ({value: elt._id, label: elt.name}))

    return (
      <React.Fragment>
         <Row >
          <Col md='12' style={this.props.style} >
            <Card>
              <CardBody className='p-1'>
                <Row>
                  {
                    !this.props.hideLevel && <Col md='4' xs='12'>
                      <Select
                        className="React mb-1"
                        classNamePrefix="select"
                        // defaultValue={levels[0]}
                        isClearable={true}
                        name="level"
                        id="level"
                        placeholder='Level'
                        value={levelOptions.find((elt) => elt.value === selectedLevelId) || ""}
                        options={levelOptions}
                        onChange={(e) => this.handleChange("selectedLevelId", (e && e.value) || "")}
                      />
                    </Col>
                  }
                  {
                    !this.props.hideCategory && <Col md='4' xs='12'>
                      <Select
                        className="React mb-1"
                        classNamePrefix="select"
                        isClearable={true}
                        // defaultValue={subjects[0]}
                        value={categoriesOptions.find((elt) => elt.value === selectedCategoryId) || ""}
                        name="category-option"
                        id="category-option"
                        placeholder='Category'
                        options={categoriesOptions}
                        onChange={(e) => this.handleChange("selectedCategoryId", (e && e.value) || "")}
                      />
                    </Col>
                  }
                  {
                    !this.props.hideTopic && <Col md='4' xs='12'>
                      <Select
                        className="React mb-1"
                        isClearable={true}
                        classNamePrefix="select"
                        // defaultValue={topics[0]}
                        value={topicsOptions.find((elt) => elt.value === selectedTopicId) || ""}  
                        name="topics"
                        id="topics"
                        placeholder='Topic'
                        options={topicsOptions}
                        onChange={(e) => this.handleChange("selectedTopicId", (e && e.value) || "")}
                      />
                    </Col>
                  }
                </Row>
                <Row>
                  {
                    !this.props.hideRangePicker && <Col md="4" xs='5'>
                      <Flatpickr
                        value={rangePicker}
                        className="form-control"
                        options={{  mode: "range"  }}
                        onChange={(dates) => this.handleChange("rangePicker", dates) }
                      />
                    </Col>
                  }
                  {
                    !this.props.hideMCQ && <Col md='2' xs='5'>
                      <div className="dropdown mr-1 d-inline-block">
                        <UncontrolledButtonDropdown>
                          <DropdownToggle
                            color="flat-info"
                            caret
                            >
                            MCQ({MCQ === "" ? "All" : MCQ})
                            <ChevronDown size={15} />
                          </DropdownToggle>
                          <DropdownMenu>
                            <DropdownItem tag="a"
                              onClick={() => this.handleChange("MCQ", "")}
                            >All</DropdownItem>
                            <DropdownItem tag="a"
                              onClick={() => this.handleChange("MCQ", "ONLY")}
                            >Only</DropdownItem>
                            <DropdownItem tag="a"
                              onClick={() => this.handleChange("MCQ", "OTHER")}
                            >Other</DropdownItem>
                          </DropdownMenu>
                        </UncontrolledButtonDropdown>
                      </div>
                    </Col>
                  }
                </Row>
              </CardBody>
            </Card>
          </Col >
        </Row>
      </React.Fragment>
    );
  }
}

SelectTopic.propTypes = {
  onChange:         PropTypes.func,
  levels:           PropTypes.array, 
  categories:       PropTypes.array,
  topics:           PropTypes.array,
  hideLevel:        PropTypes.bool,
  hideCategory:     PropTypes.bool,
  hideTopic:        PropTypes.bool,
  hideRangePicker:  PropTypes.bool,
  hideMCQ:          PropTypes.bool,
};

const mapStateToProps = (state) => ({
  levelId: state.topicSelectForm.levelId,
  topicId: state.topicSelectForm.topicId,
  categoryId: state.topicSelectForm.categoryId,
})

// const mapDispatchToProps = {
//   getLevels,
//   getCategories,
//   getTopics,
// }

export default connect(mapStateToProps, { setValues })(SelectTopic)